/* Para os desenvolvedores que encontrarem esse componente, apenas coloquem /narga no link e veja a magica
* Obs: Com o tempo irei adicionar mais Easter Eggs, fique atento xD ;)
*/
import React from 'react';
import styled from 'styled-components';
import Video from './video/video.mp4';


const NargaContainer = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
`;

const VideoPlayer = styled.video`;
    height: 100vh;
    margin: 0;
    padding: 0;
`;

const Narga = (): JSX.Element => {
	return (
		<NargaContainer>
			<VideoPlayer autoPlay loop>
				<source src={Video} type="video/mp4" />
			</VideoPlayer>
		</NargaContainer>
	);
};

export default Narga;
