/* Para os desenvolvedores que encontrarem esse componente, apenas coloquem /narga no link e veja a magica
* Obs: Com o tempo irei adicionar mais Easter Eggs, fique atento xD ;)
*/
import React                            from 'react';
import styled, { createGlobalStyle }    from 'styled-components';
import { useNavigate } 		            from 'react-router-dom';


const GlobalStyle = createGlobalStyle`
    body {
        height: 100vh;
        background-color: #f2f2f2;
        display: flex;
        justify-content: center;
        align-items: center;
    };
`;

const MenuOptions = styled.a`
    height: 5rem;
    width: 12rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: .5rem;
    font-size: 2rem;
    color: #fff;
    background: #5085a5;
    margin: 1rem;
    text-decoration: none;
    cursor: pointer;
`;


const MenuNarga = (): JSX.Element => {
	const history = useNavigate();

	return (
		<>
			<GlobalStyle />
			<MenuOptions onClick={() => history('/narga/player')}>E o Narga?</MenuOptions>
			<MenuOptions onClick={() => history('/') }>Sair</MenuOptions>
		</>
	);
};

export default MenuNarga;
